<template>
  <div>
    <div
      :class="{ 'contentView': true, 'contentViewOverflowing': ($store.state.fullScreenItems != null) }"
      style="z-index: 3"
    >

      <transition name="fade" mode="out-in">
        <keep-alive>
          <router-view :key="$route.path" />
        </keep-alive>
      </transition>
    </div>

    <DockedPlayer
      :item="$root.mediaPlayerItem"
      v-if="$root.mediaPlayerDocked"
      v-on:close="onCloseDockedPlayer()"
      v-on:maximize="onMaximizeDockedPlayer($event)"
    />

    <SharedMediaPlayer :item="$root.mediaPlayerItem" />

    <v-bottom-navigation
      fixed
      height="60px"
      width="100%"
      style="z-index: 2"
      v-model="activeTab"
      class="navigationBar text-center"
      color="accent"
      mandatory
      grow
      active-class="activeTab"
    >
      <v-btn @click="showHome" v-blur class="navButton">
        <v-icon v-blur>$vuetify.icons.logo</v-icon>
      </v-btn>
      <v-btn @click="showCategories" v-blur class="navButton">
        <v-icon>$vuetify.icons.categories</v-icon>
      </v-btn>
      <v-btn @click="showRadio" v-blur class="navButton">
        <v-icon :class="{'liveRadioIcon': liveRadioAvailable}">$vuetify.icons.radio</v-icon>
        <span v-if="liveRadioAvailable" class="notificationIcon"></span>
      </v-btn>
      <v-btn @click="showSaved" v-blur class="navButton">
        <v-icon>$vuetify.icons.favorites</v-icon>
      </v-btn>
      <v-btn @click="showMore" v-blur class="navButton">
        <v-icon>$vuetify.icons.more</v-icon>
      </v-btn>
    </v-bottom-navigation>
  </div>
</template>

<script>
import SharedMediaPlayer from "../components/SharedMediaPlayer";
import DockedPlayer from "../components/DockedPlayer";

export default {
  name: "Main",
  components: {
    SharedMediaPlayer,
    DockedPlayer
  },
  data() {
    return {
      activeTab: null,
      transitionName: null
    };
  },
  computed: {
    liveRadioAvailable() {
      return this.$store.state.liveRadio != null;
    }
  },
  watch: {
    $route(to, from) {
      const toDepth = to.path.split("/").length;
      const fromDepth = from.path.split("/").length;
      this.transitionName = toDepth < fromDepth ? "fade" : "fade";
      console.log("Transition now: " + this.transitionName);
    }
  },

  methods: {
    showHome() {
      this.activeTab = 0;
      this.$router.replace("/home");
    },

    showCategories() {
      this.$router.replace("/categories");
    },

    showSaved() {
      this.$router.replace("/saved");
    },

    showRadio() {
      this.$router.replace("/radio");
    },

    showMore() {
      this.$router.push("/more");
    },

    serviceUpdated(service) {
      this.oldActiveTab = 0;
      this.showSettings = false;
      this.$root.appInstance.componentInstance.serviceUpdated(service);
      this.showHome();
    },

    onCloseDockedPlayer() {
      console.log("onCloseDockedPlayer");
      this.$root.mediaPlayerItem = null;
      this.$root.mediaShowList = false;
      this.$root.mediaPlayerDocked = false;
    },

    onMaximizeDockedPlayer(event) {
      const {itemRoute} = event;
      this.$root.mediaShowList = true;
      this.$root.mediaPlayerDocked = false;
      this.$router.replace({name: itemRoute.name}).catch(ignoredErr => {})
    }
  }
};
</script>

