import Vue from 'vue'

import Tealium from 'tealium';
import tealiumCollect from 'tealium-collect';

// Initialize Tealium analytics
var config = {
  "account": "bbg",
  "profile": "main",
  "environment": "dev",
  "datasource": "6g2fzf"
};
const tealium = Tealium(config);
tealium.addModule(tealiumCollect);

const tealiumLogger = {
  logPageView: function (pageName) {
    tealium.track("page_view", {
      "property_id": "bbgrfadev1",
      "page_name": pageName
    });
  }
}

const tealiumPlugin = {}
tealiumPlugin.install = function (Vue, ignoredOptions) {
  console.log("Install tealium global variable");
  Vue.prototype.$tealium = tealiumLogger;
}

Vue.use(tealiumPlugin);

export default tealiumLogger;
