<template>
  <div :class="{'dockedPlayer':true, 'dockedPlayerFullscreen':($store.state.fullScreenItems != null)}" @click.stop="maximize()" v-if="$root.mediaPlayer != null">
    <v-container fluid fill-height ma-0 pa-0>
      <v-layout column ma-0 pa-0 xs12>
        <v-flex ma-0 pa-0 xs12 class="overlayControls" style="height: 70px">
          <v-container fluid fill-height pa-0 ma-0>
            <v-layout align-center justify-center pa-0 ma-0 xs12>
              <div style="width: 100%;position:absolute;top:0px" class="text-center">
                <v-btn
                  class="ma-0 pa-0"
                  text
                  small
                  icon
                  color="black"
                  style="min-width: 0;min-height: 0; position:absolute;top:0px"
                >
                  <v-icon class="tiny">$vuetify.icons.expand</v-icon>
                </v-btn>
              </div>
              <v-flex xs3 v-if="isVideo" fill-height>
                <div style="height:100%;overflow:hidden" id="videoPlaceholderDocked" />
              </v-flex>
              <v-flex ma-0 pa-0 :class="{'text-center': true, 'xs7': isVideo, 'xs10': !isVideo}">
                <div
                  class="text-left itemBody text-truncate"
                  style="max-height:var(--v-theme-body-line-height-scaled);margin-right:22px;margin-left:22px"
                >{{ itemTitle }}</div>
              </v-flex>

              <v-flex xs1 class="text-center">
                <v-btn
                  v-show="!$root.mediaPlayer.isPlaying"
                  class="large-button"
                  text
                  icon
                  color="black"
                  @click.stop="$root.mediaPlayer.play()"
                >
                  <v-icon>$vuetify.icons.play</v-icon>
                </v-btn>
                <v-btn
                  v-show="$root.mediaPlayer.isPlaying"
                  class="large-button"
                  text
                  icon
                  color="black"
                  @click.stop="$root.mediaPlayer.pause()"
                >
                  <v-icon>$vuetify.icons.pause</v-icon>
                </v-btn>
              </v-flex>

              <v-flex xs1 class="text-center mr-4">
                <v-btn class="small-button" text icon color="black" @click.stop="close()">
                  <v-icon>$vuetify.icons.close</v-icon>
                </v-btn>
              </v-flex>
            </v-layout>
          </v-container>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>


<script>
import ItemModel from "../models/itemmodel";

export default {
  props: {
    item: {
      type: ItemModel,
      default: function() {
        return new ItemModel();
      }
    }
  },
  data: () => ({
    itemRoute: null
  }),
  computed: {
    isVideo() {
      return this.item != null && this.item.hasVideoAttachment();
    },
    itemTitle() {
      if (this.item != null) {
        return this.item.title;
      }
      return "";
    }
  },
  watch: {
    item: function() {
      this.onItemChange();
    }
  },
  mounted() {
    this.onItemChange();
  },
  methods: {
    onItemChange() {
      // Record the route we are on, so we can open the same route on "Maximize".
      this.itemRoute = this.$router.currentRoute;
      console.log("Start playing on route:");
      console.log(this.itemRoute);
    },
    close() {
      this.$emit("close");
    },
    maximize() {
      this.$emit("maximize", {
        item: this.item,
        itemRoute: this.itemRoute
      });
    }
  }
};
</script>

<style scoped>
@import url("../assets/css/item-style.css");
@import url("../assets/css/shared-styles.css");

.dockedPlayer {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 30;
  transition: 0.3s;
  overflow: hidden;
  background-color: #fafafa;
  bottom: 60px;
  left: 0px;
  width: 100%;
  height: 70px;
  border: 1px solid gray;
}

.dockedPlayerFullscreen {
  bottom: 0px !important;
}

.overlayControls {
  background-color: #ffffff;
  z-index: 100;
}

.v-btn {
  min-width: 0;
  padding: 0;
}
</style>

<style>
#videoPlaceholderDocked video {
  height: 100%;
  width: auto;
  overflow: hidden;
}
</style>
