<template>
  <div v-show="true">
      <video
        v-if="item != null && ((item.isLive && hlssupported) || item.hasVideoAttachment())"
        v-renderToId:[videoPlaceholderName]
        ref="player"
        @canplay="onCanPlay"
        @loadeddata="onLoaded"
        @seeked="onSeeked"
        @pause="onPaused"
        @play="onPlay"
        @timeupdate="onTimeUpdate"
      >
        <source :src="enclosureURL" :type="enclosureType" v-if="!item.isLive" />Your browser does not support the audio tag.
      </video>
    <audio
      v-else
      v-show="false"
      class="player"
      ref="player"
      @click="showHideOverlayControls"
      @canplay="onCanPlay"
      @loadeddata="onLoaded"
      @error="onError"
      @seeked="onSeeked"
      @pause="onPaused"
      @play="onPlay"
      @timeupdate="onTimeUpdate"
    >
      <source :src="enclosureURL" :type="enclosureType" />Your browser does not support the audio tag.
    </audio>
  </div>
</template>


<script>
import MediaPlayerBase from "./MediaPlayerBase.vue";
import Hls from "hls.js";
import moment from "moment";

export default {
  extends: MediaPlayerBase,
  data: () => ({
    hls: null,
    hlsTimeAdjust: 0
  }),
  mounted() {
    console.log("Install shared player");
    this.$root.mediaPlayer = this;
    this.playerObject = this.$refs.player;
  },
  updated() {
    this.playerObject = this.$refs.player;
  },
  computed: {
    isLive() {
      return this.item != null && this.item.isLive;
    },
    videoPlaceholderName() {
      if (this.$root.mediaPlayerDocked) {
        console.log("videoPlaceholder now: videoPlaceholderDocked");
        return "videoPlaceholderDocked";
      }
        console.log("videoPlaceholder now: videoPlaceholderController");
      return "videoPlaceholderController";
    },
    isVideo() {
      return this.item != null && this.item.hasVideoAttachment();
    },

    hlssupported() {
      return Hls.isSupported();
    }

  },
  methods: {
    loadMedia() {
      console.log("SharedMediaPlayer - Load Media");
      const video = this.playerObject;
      if (this.isLive) {
        console.log("Is live");
        this.hlsTimeAdjust = 0;
        const self = this;
        if (Hls.isSupported()) {
          console.log("using hls");
          console.log("SharedMediaPlayer - Load live media");
          const hls = new Hls();
          this.hls = hls;

          // bind them together
          hls.attachMedia(video);
          hls.on(Hls.Events.MEDIA_ATTACHED, function() {
            console.log("video and hls.js are now bound together !");
            hls.loadSource(self.enclosureURL);
            hls.on(Hls.Events.MANIFEST_PARSED, function(event, data) {
              console.log(
                "manifest loaded, found " +
                  data.levels.length +
                  " quality level"
              );
              video.play();
            });
            // TODO - Wrong timezone on the timestamps?!?
            // hls.on(Hls.Events.FRAG_PARSING_METADATA, function(event, data) {
            //   console.log("** FRAG_PARSING_METADATA");
            //   console.log(event);
            //   console.log(data);
            //   const programTime = data && data.frag && data.frag.rawProgramDateTime;
            //   if (programTime) {
            //     var stamp = moment.parseZone(programTime);
            //     var now = new moment();
            //     var diff = moment.duration(now.diff(stamp));
            //     console.log("NOW: "+ now.format());
            //     console.log("PROG: "+ stamp.format());
            //     console.log("DIFF: " + diff.asSeconds());
            //     this.hlsTimeAdjust = diff.asSeconds();
            //   } else {
            //     this.hlsTimeAdjust = 0;
            //   }
            // });
          });
        } else if (video.canPlayType("application/vnd.apple.mpegurl")) {
          console.log("Can play m3u");
          this.enclosureType = "application/vnd.apple.mpegurl";
          this.$nextTick(function() {
            MediaPlayerBase.methods.loadMedia.call(self);
          });
        }
      } else {
        if (Hls.isSupported()) {
          if (this.hls != null) {
            this.hls.detachMedia();
          }
          this.hls = null;
        }
        MediaPlayerBase.methods.loadMedia.call(this);
      }
    },

    getCurrentAndDuration() {
      if (this.isLive) {
        try {
          let duration = this.item.enclosureDuration;
          var current = 0;
          var now = new moment();
          if (this.hlsTimeAdjust != 0) {
            now.subtract(this.hlsTimeAdjust, "seconds");
          }
          var start = this.$store.state.liveRadio.start;
          var diff = moment.duration(now.diff(start));
          //console.log(diff.asSeconds());
          current = diff.asSeconds();
          return { current: current, duration: duration };
        } catch (e) {
          console.log("Error getting duration and current: " + e);
        }
      }
      return MediaPlayerBase.methods.getCurrentAndDuration.call(this);
    }
  }
};
</script>
